<template>
  <!-- 选择区域和管理区域的外层容器 -->
  <view class="outter-box">
    <!-- 选择区域 -->
    <view class="choose-box">
      {{area}}:
      <view class="picker-box">
        <picker :range="arr" :value="index" @change="change">
          {{arr[index] || '暂无数据'}}
          <uni-icons class="icon-bottom" type="bottom" size="20"></uni-icons>
        </picker>
      </view>
      <!-- 设置图标 -->
      <uni-icons class="icon-setting" color="#fff" type="gear-filled" size="24" @click="showArea('college')">
      </uni-icons>
    </view>
    <!-- 管理区域 -->
    <view class="manage-box" v-show="isShow">
      <view class="item" v-for="(name,i) in arr" :key="i">{{name}}
        <!-- X 图标 -->
        <uni-icons class="icon-del" color="#6f6f6f" type="clear" size="24" @click="del(name)"></uni-icons>
      </view>
      <!-- + 图标 -->
      <uni-icons class="icon-add" color="#fff" type="plusempty" size="24" @click="add"></uni-icons>
    </view>
  </view>
</template>

<script>
  export default {
    name:"my-manage",
    props: {
      area: {
        type: String,
        default: ''
      },
      arr: {
        type: Array || null,
        default: []
      }
    },
    data() {
      return {
        // 是否展示管理区域
        isShow: false,
        // picker数组索引
        index: 0,
      };
    },
    methods: {
      // picker选择器改变
      change(e) {
        this.index = e.detail.value
        this.$emit('change',{area: this.area, index: this.index})
      },
      // 显示管理区域
      showArea(type) {
        this.isShow = !this.isShow
      },
      // 删除
      del(name) {
        let that = this
        uni.showModal({
          title: '提示',
          content: `确定要删除该项吗？`,
          cancelText: "取消", // 取消按钮的文字
          confirmText: "确认", // 确认按钮文字 
          success: function(res) {
            // 选择确定按钮
            if (res.confirm) {
              that.$emit('del',{area: that.area,name})
            }
          }
        })
      },
      // 添加
      add() {
        const that = this
        uni.showModal({
          title: '请输入' + that.area + '名称',
          editable: true, // 是否显示输入框
          placeholderText: '输入'+ that.area +'名称',
          cancelText: "取消", // 取消按钮的文字
          confirmText: "确认", // 确认按钮文字 
          success: function(res) {
            // 选择确定按钮
            if (res.confirm) {
              // console.log(res.content)
              that.$emit('add',{area: that.area,name: res.content})
            }
          }
        })
      },
    }
  }
</script>

<style lang="scss" scoped>
// 选择区域和管理区域的外层容器
  .outter-box {
    width: 85%;
    padding: 20px 10px;
    margin: 10px auto;
    border-radius: 14px;
    background-color: #20517f;

    // 选择学院区域 、选择年级区域 和 选择班级区域
    .choose-box {
      display: flex;
      justify-content: space-around;
      align-items: center;
      font-size: 18px;
      color: white;

      .picker-box {
        width: 60%;
        font-size: 16px;
        padding: 5px 10px;
        border-radius: 18px;
        border: 1px solid #333;
        color: #20517f;
        background-color: white;

        // V图标
        .icon-bottom {
          float: right;
        }
      }
    }

    // 管理区域盒子
    .manage-box {
      margin-top: 10px;

      .item {
        position: relative;
        padding: 5px;
        margin: 5px;
        color: #20517f;
        display: inline-block;
        border-radius: 18px;
        background-color: white;

        // X 图标
        .icon-del {
          position: absolute;
          right: -7px;
          top: -9px;
        }
      }

      // + 图标
      .icon-add {
        position: relative;
        top: 3px;
        right: -6px;
      }
    }
  }
</style>